import React, {useEffect, useState} from 'react';
import {useIntl} from 'react-intl';

// import AppComponentCard from '@crema/core/AppComponentCard';
import {AppComponentHeader} from '@crema';
import {Box} from '@mui/material';
import {useLocation} from 'react-router-dom';
import ReportPreviewContaner from 'components/ReportPreviewContaner';
import {getOrder} from '@crema/services/auth/firebase/firebase';

const orderDetials = () => {
  const location = useLocation('');
  const [state, setState] = useState(location.state);
  const Carts = React.lazy(() => import('../../ecommerce/Carts'));
  const UserInformation = React.lazy(() =>
    import('../../ecommerce/ClinetInformation'),
  );
  // const OrderInvoice = React.lazy(() => import('../../ecommerce/Invoice2'));

  const OrderAddress = React.lazy(() => import('../../ecommerce/OrderAddress'));
  const {messages} = useIntl();

  useEffect(async () => {
    if (!location?.state) {
      console.log(location.pathname);
      let rez = {};
      rez = await getOrder(
        '#' +
          location.pathname.split('/')[location.pathname.split('/').length - 1],
      );
      console.log(rez);
      setState({...rez});
      window.sessionStorage.setItem('items', JSON.stringify([]));
      window.sessionStorage.setItem('orderPrice', JSON.stringify({}));
      window.sessionStorage.setItem('orderStatus', JSON.stringify(rez.status));
      window.sessionStorage.setItem('order', JSON.stringify(rez));
      window.sessionStorage.setItem('Oldorder', JSON.stringify(rez));
      window.sessionStorage.setItem(
        'Informition',
        JSON.stringify(rez.BilingInformtion),
      );
      window.sessionStorage.setItem(
        'OrderAddress',
        JSON.stringify(rez.BilingInformtion),
      );
    } else {
      window.sessionStorage.setItem('items', JSON.stringify([]));
      window.sessionStorage.setItem('orderPrice', JSON.stringify({}));
      window.sessionStorage.setItem(
        'orderStatus',
        JSON.stringify(state.status),
      );
      window.sessionStorage.setItem('order', JSON.stringify(state));
      window.sessionStorage.setItem('Oldorder', JSON.stringify(state));
      window.sessionStorage.setItem(
        'Informition',
        JSON.stringify(state.BilingInformtion),
      );
      window.sessionStorage.setItem(
        'OrderAddress',
        JSON.stringify(state.BilingInformtion),
      );
    }
  }, []);
  return (
    <>
      <AppComponentHeader
        cardStyle={{height: 'auto'}}
        fullView
        title={messages['sidebar.management.orderDetials']}
      />
      {state != null && (
        <>
          <Box sx={{mb: 6}}>
            <UserInformation data={state} />
          </Box>
          <OrderAddress data={state.BilingInformtion} />
          <Box sx={{mb: 8}}>
            <Carts data={state} />
          </Box>
          <Box sx={{mb: 8}}>
            <ReportPreviewContaner data={state} component={() => <></>} />
          </Box>
        </>
      )}
    </>
  );
};
export default orderDetials;
